.border {
  border-radius: 5px 5px 5px 5px;
  margin: auto;
  width: 99%;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 30px;
}
.form {
  width: 92%;
  margin: auto;
}
.form > h3 {
  font-size: 1.3em;
  padding-top: 15px;
  margin: 0;
  font-weight: 600;
}
.button {
  outline: none;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 8px;
  background-color: rgb(42, 54, 147);
  border-radius: 3px;
  color: white;
  height: 35px;
  font-size: 1em;
  font-weight: 550;
  cursor: pointer;
}

.input {
  outline: none;
  border: 1px solid lightslategray;
  border-radius: 3px;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  height: 35px;
  padding-left: 15px;
  padding-right: 15px;
}
/* .input :focus {
  border: 1px solid rgb(42, 54, 147);
} */

input:focus {
  border: 1.5px solid rgb(42, 54, 147);
}
